/**
 * Using Rails-like standard naming convention for endpoints.
 * GET     /api/pincodes              ->  index
 * POST    /api/pincodes              ->  create
 * GET     /api/pincodes/:id          ->  show
 * PUT     /api/pincodes/:id          ->  upsert
 * PATCH   /api/pincodes/:id          ->  patch
 * DELETE  /api/pincodes/:id          ->  destroy
 */

'use strict';

import jsonpatch from 'fast-json-patch';
import Pincode from './pincode.model';

function respondWithResult(res, statusCode) {
  statusCode = statusCode || 200;
  return function(entity) {
    if(entity) {
      return res.status(statusCode).json(entity);
    }
    return null;
  };
}

function patchUpdates(patches) {
  return function(entity) {
    try {
      jsonpatch.apply(entity, patches, /*validate*/ true);
    } catch(err) {
      return Promise.reject(err);
    }

    return entity.save();
  };
}

function removeEntity(res) {
  return function(entity) {
    if(entity) {
      return entity.remove()
        .then(() => {
          res.status(204).end();
        });
    }
  };
}

function handleEntityNotFound(res) {
  return function(entity) {
    if(!entity) {
      res.status(404).end();
      return null;
    }
    return entity;
  };
}

function handleError(res, statusCode) {
  statusCode = statusCode || 500;
  return function(err) {
    res.status(statusCode).send(err);
  };
}

// Gets a list of Pincodes
export function index(req, res) {
  return Pincode.find().exec()
    .then(respondWithResult(res))
    .catch(handleError(res));
}

// Gets a single Pincode from the DB
export function show(req, res) {
  return Pincode.findById(req.params.id).exec()
    .then(handleEntityNotFound(res))
    .then(respondWithResult(res))
    .catch(handleError(res));
}

// Creates a new Pincode in the DB
export function create(req, res) {
 let pincode = new Pincode(req.body);
 pincode.save(function(err){
  if(err){
    return handleError(res);
  }
  else{
    res.json(pincode);
  }
 })
}

// Upserts the given Pincode in the DB at the specified ID
export function upsert(req, res) {
  if(req.body._id) {
    delete req.body._id;
  }
  return Pincode.findOneAndUpdate({_id: req.params.id}, req.body, {new: true, upsert: true, setDefaultsOnInsert: true, runValidators: true}).exec()

    .then(respondWithResult(res))
    .catch(handleError(res));
}


// Deletes a Pincode from the DB
export function destroy(req, res) {
  return Pincode.findById(req.params.id).exec()
    .then(handleEntityNotFound(res))
    .then(removeEntity(res))
    .catch(handleError(res));
}
